@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap");
.app {
  font-family: "Poppins", sans-serif;
  line-height: 1.5;
  color: var(--clr-fg);
  background-color: var(--clr-bg);
}

.light {
  /* --clr-bg: #fcfcfc; */
  --clr-bg:white;
  --clr-bg-alt: #fff;
  --clr-primary-teckstack:black;
  --clr-primary-timeline:black;
  --clr-primary-statics:black;
  --clr-primary-project:black;
  /* --clr-fg: #555; */
  --clr-fg: rgb(23, 133, 47);
  --clr-fg-alt: rgb(135, 40, 148);
  --clr-primary: #2978b5;
  --shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.dark {
  /* --clr-bg: #111c2c; */
  --clr-bg:  #01080f;
  --clr-bg-alt: #0e111f;
  --clr-fg: #95959b;
  --clr-fg-alt: #c1c1e4;
  --clr-primary: #8395d6;
  --clr-primary-teckstack:#c1c1e4;
  --clr-primary-timeline:#c1c1e4;
  --clr-primary-statics:#c1c1e4;
  --clr-primary-project:#c1c1e4;
  --shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

main {
  max-width: 1100px;
  width: 95%;
  margin: 0 auto;
}

.section {
  margin-top: 5em;
}

.section__title {
  text-align: center;
  margin-bottom: 2.2em;
  text-transform: uppercase;
}

.center {
  display: flex;
  align-items: center;
}

.link {
  color: var(--clr-primary);
  padding: 0 0 0.3em 0;
  position: relative;
}

.link:hover {
  color: var(--clr-primary);
}

.link::before {
  content: "";
  display: inline;
  width: 0%;
  height: 0.2em;
  position: absolute;
  bottom: 0;
  background-color: var(--clr-primary);
  transition: width 0.2s ease-in;
}

.link:hover::before,
.link:focus::before {
  width: 100%;
}

.link--nav {
  color: var(--clr-fg);
  font-weight: 500;
}

.link--icon {
  color: var(--clr-fg);
}

.btn {
  display: block;
  cursor: pointer;
  padding: 0.8em 1.4em;
  font-weight: 500;
  font-size: 0.9rem;
  transition: transform 0.2s ease-in-out;
}

.btn--outline {
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.btn--outline:hover,
.btn--outline:focus {
  color: var(--clr-bg);
}

.btn--outline:before {
  content: "";
  position: absolute;
  background-color: var(--clr-primary);
  right: 100%;
  bottom: 0;
  left: 0;
  top: 0;
  z-index: -1;
  transition: right 0.2s ease-in-out;
}

.btn--outline:hover:before,
.btn--outline:focus:before {
  right: 0;
}

.btn--plain {
  text-transform: initial;
  background-color: var(--clr-bg-alt);
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px;
  border: 0;
}

.btn--plain:hover {
  transform: translateY(-4px);
}

.btn--icon {
  padding: 0;
}

.btn--icon:hover,
.btn--icon:focus {
  color: var(--clr-primary);
}

.btn--icon:active {
  transform: translateY(-5px);
}

@media (max-width: 600px) {
  .section {
    margin-top: 4em;
  }

  .home {
    position: fixed;
    overflow: hidden;
    top: 0px;
    height: 10px;
    padding: 15px;
    font-size: 35px;
    left: 0;
    margin-bottom: 10px;
    z-index: 9999;
    box-shadow: rgba(0, 0, 0, 0.00) 0px 1px 2px 0px;
  }

}

.home {
  position: fixed;
  overflow: hidden;
  top: -10px;
  height: 87px;
  left: 0;
  margin-bottom: 10px;
  z-index: 9999;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.00) 0px 1px 2px 0px;
}
